<template>
  <div>
    <hr />
    <post-comment-bar v-on:submitcomment="postComment" v-on:error="showErrorMessage"></post-comment-bar>

    <hr />
    <!-- searc all users -->
    <input type="submit" @click="getAllResults" value="search all users" />

    <hr />
    <search-number-bar
      v-on:mysubmit="getResultsByUserId"
      placeholder="1"
      btncaption="search by userId"
    ></search-number-bar>

    <hr />
    <search-number-bar v-on:mysubmit="getResultsById" placeholder="1" btncaption="search by Id"></search-number-bar>

    <hr />
    <!-- show search result -->
    <div class="search-result">
      <div
        class="search-result__item rounded"
        v-for="(item, index) in searchedResult"
        v-bind:key="index"
        @click="selectedItem=item"
      >
        <span class="search-result__Id">Id: {{ item.id }}</span>
        <span class="search-result__title">title: {{ item.title }}</span>
        <br />
        <p class="search-result__body">{{item.body}}</p>
        <div class="search_item-footer">
          <button @click="deleteComment(item.id)">Delete</button>
          <button @click="isVisibleModalEdit = true">Edit</button>
          <span class="search-result__user-id">userId: {{item.userId}}</span>
        </div>
      </div>
    </div>

    <!-- modals -->
    <modal-window
      v-on:close="isVisibleModalEdit=false"
      :data="selectedItem"
      :visibility="isVisibleModalEdit"
    ></modal-window>

    <modal-message :visibility="isVisibleModalMessage" :messages="errorMessages"></modal-message>
  </div>
</template>

<script>
import searchNumberBar from "./searchNumberBar.vue";
import postCommentBar from "./postCommentBar.vue";

import modalWindow from "./modal.vue";
import modalMessage from "./modalMessage";

export default {
  data() {
    return {
      searchedResult: [],
      selectedItem: {},
      isVisibleModalEdit: false,
      isVisibleModalMessage: true,
      errorMessages: []
    };
  },
  methods: {
    testMethod: function(value) {
      console.log("test method");
      console.log(value);
    },
    getAllResults: function() {
      axios.get("http://localhost:3000/db").then(response => {
        this.searchedResult = response.data;
      });
    },
    getResultsById: function(id) {
      axios
        .get("http://localhost:3000/db", {
          params: { id: id }
        })
        .then(response => {
          this.searchedResult = response.data;
        });
    },
    getResultsByUserId: function(id) {
      axios
        .get("http://localhost:3000/db", {
          params: { userId: id }
        })
        .then(response => {
          this.searchedResult = response.data;
        });
    },
    postComment: function(obj) {
      let thisObj = this;

      axios
        .post("http://localhost:3000/db", obj)
        .then(response => {
          console.log("response");
        })
        .catch(function(error) {
          // thisObj.errorMessages = "   !";
        });
    },
    deleteComment: function(ind) {
      axios
        .delete("http://localhost:3000/db", {
          params: { id: ind }
        })
        .then(response => {
          console.log(response.data);
        });
    },
    showErrorMessage: function(errors = []) {
      let thisObj = this;

      this.errorMessages = errors;
      this.isVisibleModalMessage = true;

      setTimeout(() => {
        thisObj.isVisibleModalMessage = false;
        thisObj.errorMessages = [];
      }, 2000);
    }
  },
  watch: {},
  components: {
    searchNumberBar,
    postCommentBar,
    modalWindow,
    modalMessage
  }
};
</script>

<style>
.search-result__item {
  border: 1px solid #000;
  margin: 5px 0;
  cursor: pointer;
  transition: transform 0.2s;
  background-color: #fff;
}
.search-result__item:hover {
  transform: scale(1.1);
}

.search-result__Id {
  background-color: cornflowerblue;
  color: white;
  padding: 2px 4px;
}
.search-result__title {
  padding-left: 15px;
  font-weight: 600;
}
.search_item-footer {
  display: flex;
  justify-content: flex-end;
  padding: 5px;
}
.search-result__user-id {
  padding: 0 9px;
  font-style: italic;
}

/* .search-result__body {
  width: 100%;
} */

textarea {
  width: 100%;
  resize: none;
}
</style>



