<template>
  <div>
    <label for>:</label>
    <input type="text" v-model="login" size="16" maxlength="16" />
    <label for>:</label>
    <input type="password" v-model="password" size="16" maxlength="16" />
    <label for> :</label>
    <input type="password" v-model="repeatPassword" size="16" maxlength="16" />
    <input type="submit" @click="submitRegistration" value="" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      login: "",
      password: "",
      repeatPassword: "",
      errors: []
    };
  },
  methods: {
    isValidData: function() {
      this.errors = [];

      if (this.login.length < 5 || this.password.length < 5) {
        this.errors.push("       5 ");
      } else if (this.password !== this.repeatPassword) {
        this.errors.push("   ");
      }
        
      if (Number.isInteger(+this.login[0])) {
        this.errors.push("     ");
      }

      if (this.errors.length > 0) {
        return false;
      } else {
        return true;
      }
    },
    submitRegistration: function() {
      if (this.isValidData()) {
        this.$emit("submitregistration", {
          login: this.login,
          password: this.password
        });
      } else {
        this.$emit("error", this.errors);
      }
    }
  }
};
</script>

<style>
</style>


